@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;800&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

.ContainerBody {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  min-height: 100vh;
  background: #222831;
  /* margin: 20%; */
}
.mainBody  h1{
  display: flex;
   justify-content: center;
  align-items: center;
  /* background: #232427; */
  flex-wrap:wrap;
  margin-top: 20%;
  color: #EEEEEE;
  text-shadow: #EEEEEE 1px 0 10px;
}
.mainBody  h3{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  flex-wrap:wrap;
  color: #EEEEEE;
  /* margin: 2px; */
  
}

.ContainerBody .container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 40px 0;
}

.ContainerBody .container .card {
  position: relative;
  min-width: 320px;
  height: 440px;
  box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2),
    inset -5px -5px 15px rgba(255, 255, 255, 0.1),
    5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1);
  border-radius: 15px;
  margin: 30px;
  transition: 0.5s;
}

.ContainerBody .container .card:nth-child(1) .box .content a {
  background: #2196f3;
}

.ContainerBody .container .card:nth-child(2) .box .content a {
  background: #e91e63;
}

.ContainerBody .container .card:nth-child(3) .box .content a {
  background: #23c186;
}

.ContainerBody .container .card .box {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  background: #2a2b2f;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: 0.5s;
}
.ContainerBody .container .card .box img {
  width: 100%;
  height: 100%;
  position: absolute;
  /* top: 20px; */
  /* left: 20px; */
  /* right: 20px; */
  /* bottom: 20px; */
  /* background: #2a2b2f; */
  /* border-radius: 15px; */
  /* display: flex;/ */
  /* justify-content: center; */
  /* align-items: center; */
  /* overflow: hidden; */
  /* transition: 0.5s; */

}

.ContainerBody .container .card .box:hover {
  transform: translateY(-50px);
}

.ContainerBody .container .card .box:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  background: rgba(255, 255, 255, 0.03);
}

.ContainerBody .container .card .box .content {
  padding: 20px;
  text-align: center;
}

.ContainerBody .container .card .box .content h2 {
  position: absolute;
  top: -10px;
  right: 30px;
  font-size: 8rem;
  color: rgba(255, 255, 255, 0.1);
}

.ContainerBody .container .card .box .content h3 {
  font-size: 1.8rem;
  color: #fff;
  z-index: 1;
  transition: 0.5s;
  margin-bottom: 15px;
}

.ContainerBody .container .card .box .content p {
  font-size: 1rem;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.9);
  z-index: 1;
  transition: 0.5s;
}

.ContainerBody .container .card .box .content a {
  position: relative;
  display: inline-block;
  padding: 8px 20px;
  background: black;
  border-radius: 5px;
  text-decoration: none;
  color: white;
  margin-top: 20px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
}
.ContainerBody .container .card .box .content a:hover {
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.6);
  background: #fff;
  color: #000;
}

@media screen and (max-width: 390px) {

  .ContainerBody .container .card {
    position: relative;
    min-width: 250px;
    height: 380px;
    box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2),
      inset -5px -5px 15px rgba(255, 255, 255, 0.1),
      5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    margin: 30px;
    transition: 0.5s;
  }
  .mainBody  h3{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px;
    flex-wrap:wrap;
    color: #EEEEEE;
    margin: 2px;
    font-size: 0.5rem;
    
  }
}
@media screen and (max-width: 314px) {

  .ContainerBody .container .card {
    position: relative;
    min-width: 210px;
    height: 350px;
    box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.2),
      inset -5px -5px 15px rgba(255, 255, 255, 0.1),
      5px 5px 15px rgba(0, 0, 0, 0.3), -5px -5px 15px rgba(255, 255, 255, 0.1);
    border-radius: 15px;
    margin: 30px;
    transition: 0.5s;
  }
  .mainBody  h3{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap:wrap;
    color: #EEEEEE;
    margin: 10px;
    font-size: 0.7rem;
    text-wrap: wrap;
    
  }
  .ContainerBody .container .card .box .content h3 {
    font-size: 1.2rem;
    color: #fff;
    z-index: 1;
    transition: 0.5s;
    margin-bottom: 15px;
  }
  .ContainerBody .container .card .box .content a {
    position: relative;
    display: inline-block;
    padding: 4px 10px;
    background: black;
    border-radius: 5px;
    text-decoration: none;
    color: white;
    margin-top: 20px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    transition: 0.5s;
  }
}